import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import { HashRouter, Switch, Route } from "react-router-dom";

// Importing Views
import HomeApp from "../views/HomeApp";
import RegisterApp from "../views/RegisterApp";
import ProfileApp from "../views/ProfileApp";

// Importing essential scripts
import { AuthProvider } from "./firebase/auth";
import PrivateRoute from "./privare-route";

function App() {
  return (
    <AuthProvider>
      <HashRouter basename="/NorthHacks/#/home">
        <Switch>
          <Route
            path={["/NorthHacks/#/", "/NorthHacks/#/home"]}
            exact
            component={HomeApp}
          />
          <Route
            path={[
              "/NorthHacks/#/register",
              "/NorthHacks/#/signup",
              "/NorthHacks/#/sign-up",
            ]}
            exact
            component={RegisterApp}
          />
          <PrivateRoute
            path="/NorthHacks/#/profile"
            exact
            component={ProfileApp}
          />
        </Switch>
      </HashRouter>
    </AuthProvider>
  );
}

export default App;
